'use strict';
// Constants

// Load Page
WUI.ready = function () {

  WUI.ContentHeader.create({
    $el: $('.content-header'),
    meta: [{
      name: '任务'
    }]
  });

  var queryData = function(options){

    WUI.ajax({
      url: '/data/table',
      method: 'GET',
      data:{

      }
    }).done(function (resp) {
      WUI.riskTable.create({
        $el: $('.data-table'),
        titles: resp.data.titles,
        list: resp.data.data
      });
    });

  }

  queryData();
  WUI.DataPaginator.create({
    $el: $('.data-paginator'),
    currentPage: options.currentPage,
    total: resp.total,
    pageSize: options.pageSize,
    onSwitchPage: function (currentPage) {
      queryData({
        currentPage: currentPage,
        pageSize: options.pageSize
      });
    }
  });



}
// Keep this function
$(function () {
  WUI.init({
    system: 'sensitive'
  });
});

